<template>
    <div class="like">
    <van-nav-bar
      title="我的收藏"
      left-text="返回"
      left-arrow
      fixed
      @click-left="goback"
     />

     <BgBox>
            <div class="info clearfix" v-if="likeList.length > 0">
              <div class="item fl" v-for="(item,index) in likeList" :key="index">
                  <div class="img">
                      <img :src="item.smallImg" alt="" class="auto-img"  @click="goDetail(item.pid)">
                  </div>
                  <div class="name">{{item.name}}</div>
                  <div class="price">
                      <div class="left">￥{{item.price}}</div>
                      <div class="right"><van-icon name="delete" size='18' @click="removeLikeProduct(item.pid,index)"/></div>
                  </div>
              </div>
             
             
            </div>
            <div v-else class="empty">
            <van-empty description="没有收藏商品" />
            </div>
     </BgBox>

    </div>
</template>

<script>
import '../assets/less/like.less';
import BgBox from '../components/BgBox.vue'
    export default {
        name:'Like',
        components:{
            BgBox,
        },
        data(){
            return{
              // 收藏的商品数据
                likeList:[]
            }
        },
        created() {
            this.getLikeProduct()
        },
        methods: {
            goback(){
                this.$router.go(-1);
            },
             //获取收藏商品
    getLikeProduct() {
      let tokenString = localStorage.getItem("tokenString");

      if (!tokenString) {
        //跳回登录页面
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "/findAllLike",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 700) {
            this.$router.push({ name: "Login" });
          } else if (result.data.code == 2000) {
            this.likeList = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },
     //查看商品详情
    goDetail(pid) {
      this.$router.push({name: 'Detail', params: {pid}});
    },

    //删除收藏商品
    removeLikeProduct(pid, index) {
      //获取token
        let tokenString = localStorage.getItem('tokenString');
        if (!tokenString) {
          //跳回登录页面
          this.$toast('请先登录');
          return this.$router.push({name: 'Login'});
        }

        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0
        });

        //发起删除商品请求
        this.axios({
          method: 'POST',
          url: '/notlike',
          data: {
            appkey: this.appkey,
            pid,
            tokenString
          }
        }).then(result => {
          console.log(index)
          this.$toast.clear();
          if (result.data.code == 700) {
            //token检验无效,则跳到登录页面
            this.$router.push({name: 'Login'});
          } else if (result.data.code == 900) {
             this.$toast('删除成功');
             this.likeList.splice(index, 1);
          } else {
            this.$toast('删除失败');
          }

         
        }).catch(err => {
          this.$toast.clear();
          console.log('err ==> ', err);
        })
    }
  },
    }
</script>

<style lang="less" scoped>

</style>